import { _decorator, Component, Label } from 'cc';
import { myLog } from './myLog';
const { ccclass, property } = _decorator;

@ccclass('DebugLogManager')
export class DebugLogManager extends Component {
    logLabel: Label = null; // 在编辑器中将 Label 组件拖到这里

    private logMessages: string[] = [];

    onLoad() {
        this.logLabel  = this.node.getComponent(Label);
        
        // 保存原始的 console.log 方法
        // const originalLog = console.log;
        const originalLog = myLog.d;

        // 重写 console.log 方法
        // console.log = (...params: any[]) => {
        myLog.d = (...params: any[]) => {
            // 将日志信息转换为字符串
            const message = params.join(' ');

            // 将日志信息添加到数组
            this.logMessages.push(message);

            // 如果日志条数超过 10 条，删除最早的一条
            if (this.logMessages.length > 100) {
                this.logMessages.shift(); // 删除最早的一条
            }

            // 更新 Label 显示
            this.updateLogDisplay();

            params.unshift(new Date().toLocaleTimeString());

            // 调用原始的 console.log 方法以保持控制台输出
            // originalLog.apply(console, params);
            originalLog(params);
        };
    }

    private updateLogDisplay() {
        // 将所有日志信息连接成字符串并显示在 Label 中
        if (this.logLabel) {
            this.logLabel.string = this.logMessages.join('\n'); 
        }
    }
}